.petals-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    overflow: hidden;
    z-index: 9999;
  }
  
  .petal {
    position: absolute;
    top: -50px;
    animation: fall linear infinite, sway ease-in-out infinite;
  }
  
  .petal img {
    display: block;
  }
  
  @keyframes fall {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(100vh);
    }
  }
  
  @keyframes sway {
    0%, 100% {
      transform: translateX(0) rotate(0);
    }
    50% {
      transform: translateX(40px) rotate(20deg);
    }
  }